import { Toast, Picker } from '@antmjs/vantui'

const columns = ['', '', '', '', '']
export default function Demo() {
  const onChange = (event) => {
    const { value, index } = event.detail
    Toast.show(`${value}, ${index}`)
  }
  return (
    <>
      <Toast />
      <Picker
        showToolbar
        title=""
        defaultIndex={2}
        columns={columns}
        onChange={onChange}
        onCancel={() => Toast.show('onCancel')}
        onConfirm={() => Toast.show('onConfirm')}
      />
    </>
  )
}
